*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  font-family: sans-serif;
  font-size: 16px;
  line-height: 1.2;
}
body {
  background-color: #333;
  color: #fff;
  margin: 0;
  overflow: hidden;
  overscroll-behavior-y: contain;
}
.info {
  color: #ff2;
}
.success {
  color: #2f2;
}
.danger {
  color: #f22;
}
.messages {
  font-size: 22px;
  line-height: 1;
  min-height: 1em;
  margin-bottom: 12px;
}
.score {
  font-size: 18px;
  margin-bottom: 12px;
}
#root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 80vh;
}
